.reviews {
  @media (min-width: $tablet-width) {
    position: relative;

    min-height: 460px;

    background-image: url("../img/reviews.jpg");
    background-repeat: no-repeat;
  }

  @media (min-width: $desktop-width) {
    background: none;
  }
}

.reviews__container {
  display: grid;

  @media (min-width: $tablet-width) {
    grid-template-columns: 1fr 175px;
  }

  @media (min-width: $desktop-width) {
    height: 457px;

    background-image: url("../img/reviews.jpg");
    background-repeat: no-repeat;
    background-position: top left 50px;
    background-size: 350px 457px;
  }
}

.reviews__title {
  font-size: 25px;
  line-height: 30px;
  text-align: start;
  color: $black;
}

// .reviews__list {
// }

.reviews__item {
  display: none;
}

.reviews__item--active {
  display: block;
}

.reviews__desc {
  font-size: 15px;
  line-height: 30px;
}

//.reviews__author {
//}
.reviews__name {
  font-size: 17px;
  line-height: 24px;
  color: $black;
}

.reviews__email {
  font-size: 17px;
  line-height: 24px;
}

.reviews__button {
  order: 1;
  width: 260px;
  height: 52px;
  margin-top: 10px;

  @media (min-width: $tablet-width) {
    width: 175px;

    grid-column: 2/-1;
    grid-row: 1/2;
  }

  @media (min-width: $tablet-width) {
    width: 175px;

    grid-column: 2/-1;
    grid-row: 1/2;
  }
}

.reviews__control {
  position: relative;

  margin-top: 70px;
}

.reviews__control-arrow {
  width: 130px;
  height: 52px;

  background-color: transparent;
  background-position: center;
  border-color: transparent;
}

.reviews__control-arrow--prev {
  background-image: url("../img/svg/arrow-left.svg");
  background-repeat: no-repeat;
}

.reviews__control-arrow--next {
  background-image: url("../img/svg/arrow-right.svg");
  background-repeat: no-repeat;
}
